import svg4everybody from 'svg4everybody';
import $ from 'jquery';
import 'fullpage.js';
import {TimelineMax, Elastic} from 'gsap';
import '../blocks/fullpage/fullpage.js';
import '../blocks/menu/menu.js';
import '../blocks/preload/preload.js';

$(() => {
	svg4everybody();
});

	let intro = $('.header__intro');
	let media = $('.header__media');
	let reads = $('.reads__content .reads__wrap');
	let block1 = $('.present-gallery__part_js-1');
	let block2 = $('.present-gallery__part_js-2');
	let block3 = $('.present-gallery__part_js-3');
	let block4 = $('.present-gallery__part_js-4');
	let feedback = $('.feedback__content');


var contentReveal = function() {
	let tls = new TimelineMax({paused:true});
	
	tls
		.fromTo(intro, 1, {x:-200,opacity: 0, }, {x: 0,opacity: 1, ease: Bounce.easeOut, immediateRender: true})
		.fromTo(media, 1, {x:200,opacity: 0, }, {x: 0,opacity: 1, ease: Bounce.easeOut, immediateRender: true})
		.staggerFromTo(reads, 3, {opacity:0,rotation: 500}, {opacity: 1,rotation: 0, scale: 1,  ease: Bounce.easeOut, immediateRender: true})
		.from(feedback, 1, { scale: 0, ease: Bounce.easeOut, immediateRender: true});
};





let tl = new TimelineMax

tl	
